
import {defineComponent} from 'vue';

export default defineComponent({
  name: "Setting",
  emits: ['setParam', 'run'],
  data() {
    return {
      level: 1,
      rowsLength: 9,
      colsLength: 9,
      bombsCount: 10,
    };
  },
  methods: {
    run() {
      this.$emit('setParam', {rowsLength: this.rowsLength, colsLength: this.colsLength, bombsCount: this.bombsCount})
      this.$emit('run')
    }
  },
  watch: {
    level(val) {
      switch (val) {
        case 1:
          this.rowsLength = 9;
          this.colsLength = 9;
          this.bombsCount = 10;
          break;
        case 2:
          this.rowsLength = 16;
          this.colsLength = 16;
          this.bombsCount = 40;
          break;
        case 3:
          this.rowsLength = 16;
          this.colsLength = 30;
          this.bombsCount = 99;
          break;
      }
    }
  }
})
